<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-col
        cols="12"
        sm="8"
        md="4"
      >
        <template v-if="error">
          <v-alert type="error">
            Не верная пара: логин/пароль.
          </v-alert>
        </template>
        <v-card class="elevation-12">
          <v-toolbar
            color="primary"
            dark
            flat
          >
            <v-toolbar-title>Login form</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field
                v-model="login"
                label="Login"
                name="login"
                prepend-icon="mdi-account-box"
                type="text"
              />

              <v-text-field
                v-model="password"
                id="password"
                label="Password"
                name="password"
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="show = !show"
                :type="show ? 'text' : 'password'"
                prepend-icon="mdi-lock"
              />
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn color="primary" @click="logIn">Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import arrColors from '@/resource/colors.json';

export default {
  name: 'Home',
  data() {
    return {
      show: false,
      error: false,
      account: 'user',
      pass: '123',
      login: '',
      password: '',
    };
  },
  methods: {
    logIn() {
      if (this.account === this.login && this.pass === this.password) {
        this.error = false;

        this.$store.dispatch('user/setUser', {
          user: this.account,
        });
        this.$store.dispatch('colors/setColors', arrColors);
        this.$router.push({ path: '/colors' });
      } else {
        this.error = true;
      }
    },
  },
};
</script>
