const account = {
  namespaced: true,
  state: {
    user: null,
    status: 0,
  },
  getters: {
    getUser: (state) => state.user,
  },
  actions: {
    setUser({ commit, dispatch }, user) {
      dispatch('setStatus', 1);
      commit('SET_USER', user);
    },
    logout({ commit }) {
      commit('LOGOUT');
    },
    setStatus({ commit }, num) {
      commit('SET_STATUS', num);
    },
  },
  mutations: {
    SET_STATUS(state, s) {
      state.status = s;
    },
    SET_USER(state, user) {
      state.user = user;
    },
    LOGOUT(state) {
      state.user = null;
    },
  },
};
export default account;
