export default {
  namespaced: true,
  state: {
    colors: [{
      name: 'Black',
      hex: '#000',
    }],
  },
  actions: {
    setColors({ commit }, arrColor) {
      if (Array.isArray(arrColor) && arrColor.length) {
        commit('SET_COLORS', arrColor);
      }
    },
  },
  mutations: {
    SET_COLORS(state, colors) {
      state.colors = colors;
    },
  },
};
